import React, { useState, useEffect } from 'react';
import Box from '@material-ui/core/Box'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'
import Grid from '@mui/material/Grid'
import { Flex } from '@chakra-ui/react';

import ImageFadeIn from '../components/ImageFadeIn';


const useStyles = makeStyles((theme) => ({
    customImage: {
        position: 'absolute',
        alignItems: 'center',
        top: '40%',
        width: '70%',
    },
    customTypography: {
        position: 'absolute',
        alignItems: 'center',
        top: '100%',
        width: '70%',
        backgroundColor: '#FFF1E0', // 背景色を設定
        color: '#5D576B', // テキストの色を設定
        padding: theme.spacing(2), // 必要に応じて余白を設定
    },
}));

const Home: React.FC = () => {
    const classes = useStyles();
    const [isVisible, setIsVisible] = useState(false);

    useEffect(() => {
        // コンポーネントがマウントされた後に0.2秒待機してからフェードインをトリガー
        const timeout = setTimeout(() => {
            setIsVisible(true);
        }, 200); // 0.2秒待機

        // コンポーネントがアンマウントされたらタイムアウトをクリア
        return () => clearTimeout(timeout);
    }, []);
    return (
        <>
            <Grid
                container
                justifyContent="center" // 要素を水平方向に中央に配置
                alignItems="center" // 要素を垂直方向に中央に配置
            >
                <Box className={classes.customImage}>
                    <ImageFadeIn />
                </Box>
            </Grid>

            <Grid
                container
                justifyContent="center" // 要素を水平方向に中央に配置
                alignItems="center" // 要素を垂直方向に中央に配置
                style={{ height: '100vh' }}
            >

                <Typography variant='body1' align='left' className={classes.customTypography}>
                    Testのテキスト<br />
                    Testのテキスト<br />
                    Testのテキスト<br />
                    Testのテキスト<br />
                    Testのテキスト<br />
                    Testのテキスト<br />
                    Testのテキスト<br />
                </Typography>

                <section id='about' />
            </Grid >
        </>
    )

}

export default Home;