import React, { useState, useEffect } from 'react';
import Box from '@mui/material/Box';

import { makeStyles } from '@material-ui/core/styles'
import './styles.css'; // 上記で定義したCSSスタイルをインポート
import ImageFile from '../static/images/Ryouran-festival_top.png'

const useStyles = makeStyles((theme) => ({
    customImage: {
        width: '100%',
    },
}));


function ImageFadeIn() {
    const [isVisible, setIsVisible] = useState(false);
    const classes = useStyles();

    useEffect(() => {
        // コンポーネントがマウントされた後に0.2秒待機してからフェードインをトリガー
        const timeout = setTimeout(() => {
            setIsVisible(true);
        }, 200); // 0.2秒待機

        // コンポーネントがアンマウントされたらタイムアウトをクリア
        return () => clearTimeout(timeout);
    }, []);

    return (
        <Box className={`fade-in ${isVisible ? 'fade-in-entered' : 'fade-in-entering'}`} >
            <img src={ImageFile} alt="Your Image" className={classes.customImage} />
        </Box >
    );
}

export default ImageFadeIn;